

.aboutt {
    background: url('pexels-alena-koval-886521.jpg') center center/cover no-repeat;
    height: 85vh;
    width: 99vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
    
  }
  
  .aboutt > ol >li{
    font-weight:normal;
}


  .aboutt > h4 {
    text-align: left;
    color: #000000;
  
    font-size: 40px;
    margin-top: -100px;
    margin-left: 100px;
  }
  
  .aboutt > p {
    margin-top: 10px;
    justify-content: left;
    margin-left: 100px;
    margin-right: 400px;
    color: #000000;
    font-size: 27px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  .hero-btns {
    margin-top: 32px;
    margin-left: 100px;
    color: #000000;
  }
  
  .hero-btns .btn {
    margin: 6px;
  }
  
  .fa-play-circle {
    margin-left: 4px;
  }
  
  @media screen and (max-width: 960px) {
    .hero-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .hero-container > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .hero-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }
  